import { getFromWorkbookSettings } from "../taskpane/taskpane";

Office.onReady((info) => {
  if (info.host === Office.HostType.Excel) {
    Excel.run(async (context) => {
      const disableCalculationOverride = await getFromWorkbookSettings("disableCalculationOverride");
      if (disableCalculationOverride !== "true") {
        context.application.calculationMode = Excel.CalculationMode.manual;
        console.log("Calculation mode set to manual");
        await context.sync();
      } else {
        console.log("Calculation mode not overridden");
      }
    }).catch((error) => {
      console.error("Error during Excel initialization:", error);
    });
  }
});

const fnValueToFormula = {
  ARBOR_DEEP: (cellValue: Excel.EntityCellValue) => {
    if (!cellValue["properties"]["ticker"] || !cellValue["properties"]["query"]) {
      return null;
    }
    const ticker = cellValue["properties"]["ticker"]?.basicValue;
    const query = cellValue["properties"]["query"]?.basicValue;
    const isLong = cellValue["properties"]["isLongAnswer"]?.basicValue;
    return `=ARBOR_DEEP("${ticker}", "${query}", ${isLong ? "TRUE" : "FALSE"})`;
  },
  ARBOR_FAST: (cellValue: Excel.EntityCellValue) => {
    if (!cellValue["properties"]["ticker"] || !cellValue["properties"]["query"]) {
      return null;
    }
    return `=ARBOR_FAST("${cellValue["properties"]["ticker"]?.basicValue}", "${cellValue["properties"]["query"]?.basicValue}")`;
  },
  ARBOR_NUMBER: (cellValue: Excel.EntityCellValue) => {
    if (!cellValue["properties"]["ticker"] || !cellValue["properties"]["query"]) {
      return null;
    }
    return `=ARBOR_NUMBER("${cellValue["properties"]["ticker"]?.basicValue}", "${cellValue["properties"]["query"]?.basicValue}")`;
  },
  ARBOR_SCREEN: (cellValue: Excel.EntityCellValue) => {
    if (!cellValue["properties"]["query"]) {
      return null;
    }
    return `=ARBOR_SCREEN("${cellValue["properties"]["query"]?.basicValue}")`;
  },
  ARBOR_GPT_LIST: (cellValue: Excel.EntityCellValue) => {
    if (!cellValue["properties"]["query"]) {
      return null;
    }
    return `=ARBOR_GPT_LIST("${cellValue["properties"]["query"]?.basicValue}")`;
  },
  ARBOR_GPT: (cellValue: Excel.EntityCellValue) => {
    if (!cellValue["properties"]["query"]) {
      return null;
    }
    return `=ARBOR_GPT("${cellValue["properties"]["query"]?.basicValue}")`;
  },
};

async function recalculate(event: Office.AddinCommands.Event) {
  console.log("Recalculating");
  await Excel.run(async (context: Excel.RequestContext) => {
    const cellsToCalculate = [];

    try {
      const range = context.workbook.getSelectedRange();
      range.load("valuesAsJson");
      range.load("formulas");
      await context.sync();
      const formulas = range.formulas;
      range.valuesAsJson.forEach((row, rowIndex) => {
        row.forEach((cellValue, columnIndex) => {
          const cell = range.getCell(rowIndex, columnIndex);
          cell.load("address");
          cellsToCalculate.push({
            cell: cell,
            formula: formulas[rowIndex][columnIndex],
            cellValue: cellValue,
          });
        });
      });
      await context.sync();
    } catch {
      const ranges = context.workbook.getSelectedRanges();
      ranges.load("areas");
      ranges.load("cellCount");
      await context.sync();
      if (ranges.cellCount > 1638400 || ranges.cellCount < 1) {
        console.log(`Cell count ${ranges.cellCount} is out of range`);
        return;
      }
      const areas = ranges.areas;
      areas.load("items");
      await context.sync();
      for (const range of areas.items) {
        range.load("valuesAsJson");
        range.load("formulas");
      }
      await context.sync();

      for (const range of areas.items) {
        const formulas = range.formulas;
        range.valuesAsJson.forEach((row, rowIndex) => {
          row.forEach((cellValue, columnIndex) => {
            const cell = range.getCell(rowIndex, columnIndex);
            cell.load("address");
            cellsToCalculate.push({
              cell: cell,
              formula: formulas[rowIndex][columnIndex],
              cellValue: cellValue,
            });
          });
        });
      }
      await context.sync();
    }
    console.log(`Recalculating ${cellsToCalculate.length} cells`);
    const processedCells = new Set<string>();
    for (const cellObj of cellsToCalculate) {
      const { cell, formula, cellValue } = cellObj;
      if (processedCells.has(cell.address)) {
        console.log(`Cell ${cell.address} already processed`);
        continue;
      }
      if (formula.startsWith("=")) {
        cell.calculate();
        console.log(`Calculated cell ${cell.address}`);
        processedCells.add(cell.address);
        await context.sync();
        continue;
      }
      if (!cellValue || !cellValue["properties"] || !cellValue["properties"]["type"]) {
        console.log(`No cell value found for cell ${cell.address}`);
        continue;
      }
      const fnType = cellValue["properties"]["type"];
      const fn = fnValueToFormula[fnType.basicValue as keyof typeof fnValueToFormula];
      if (!fn) {
        console.log(`No formula found for cell ${cell.address}`, fnType.basicValue);
        continue;
      }
      const newFormula = fn(cellValue);
      if (newFormula) {
        console.log(`Setting formula for cell ${cell}`, cell);
        cell.formulas = [[newFormula]];
        cell.calculate();
        await context.sync();
        processedCells.add(cell.address);
      }
    }
    event.completed();
  }).catch((error) => {
    console.error("Error during recalculate:", error);
  });
  console.log("Recalculation completed");
}

Office.actions.associate("recalculate", recalculate);

async function storeAnswer(event: Office.AddinCommands.Event) {
  await Excel.run(async (context) => {
    try {
      const range = context.workbook.getSelectedRange();
      range.load("hasSpill");
      await context.sync();
      if (range.hasSpill) {
        const spillRange = range.getSpillingToRange();
        spillRange.copyFrom(spillRange, Excel.RangeCopyType.values, true);
      } else {
        range.copyFrom(range, Excel.RangeCopyType.values, true);
      }
      await context.sync();
    } catch {
      const ranges = context.workbook.getSelectedRanges();
      ranges.load("areas");
      ranges.load("cellCount");
      await context.sync();
      if (ranges.cellCount > 1638400 || ranges.cellCount < 1) {
        return;
      }
      const areas = ranges.areas;
      areas.load("items");
      await context.sync();
      for (const range of areas.items) {
        range.load("hasSpill");
      }

      await context.sync();
      for (const range of areas.items) {
        if (range.hasSpill) {
          const spillRange = range.getSpillingToRange();
          spillRange.copyFrom(spillRange, Excel.RangeCopyType.values, true);
        } else {
          range.copyFrom(range, Excel.RangeCopyType.values, true);
        }
      }
      await context.sync();
    }
  }).catch((error) => {
    console.error("Error during storeAnswer:", error);
  });
  event.completed();
}

Office.actions.associate("storeAnswer", storeAnswer);
